:root {
  --main-color: rgb(11, 10, 2);
  --secand-color: rgba(194, 192, 192, 0.199);
}
.Navbar-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(143, 85, 85, 0.199);
  height: 10vh;
  padding: 30px 0px;
  width: 100vw;
  overflow: hidden;
  padding: 0 20px;
  color: rgb(20, 19, 17);
}
.Navbar-section .Navbar-image {
  width: 140px;
  height: 10vh;
}

.Navbar-section .show-icon {
  display: flex;
  margin-right: 15px;
  margin-left: auto;
}

.Navbar-section .select-element {
  display: flex;
  width: 10%;
}
.Navbar-section .select-element li {
  padding: 20px 10px;
  cursor: pointer;
  color: var(--main-color);
  text-transform: capitalize;
  font-size: 0.9em;
  text-transform: capitalize;
  font-weight: bold;
}
.Navbar-section .select-element li:hover {
  color: black;
  border-bottom: 2px solid black;
}
.icon-bar {
  cursor: pointer;
  display: none;
}

.icon-serch-container {
  display: flex;
  width: 40%;
  margin-left: auto;
}

.search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 6vh;
  margin-left: auto;
}
.search-icon input {
  width: 200px;
  border-radius: 10px;
  border: none;
  height: 100%;
  padding: 10px;
  background-color: rgba(192, 192, 192, 0.11);
  border: 0.1px solid silver;
}
.search-icon input:hover {
  width: 250px;
  transition: all 0.5s;
}

.header-page {
  padding: 15px 30px;
  color: var(--main-color);
  font-size: 1.1em;
  text-transform: uppercase;
  text-decoration: dashed;
}

.paersonal-bar-container {
  position: absolute;
  top: 65px;
  right: 0;
  background-color: rgb(228, 228, 228);
  z-index: 6000;
  width: 250px;
  padding: 15px;
}
.paersonal-bar-container img {
  width: 50px;
  height: 50px;
}
.paersonal-bar-container .header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Navbar-image {
  position: absolute;
  left: 45%;
  top: 0;
  transform: translate(-100%, -50%);
  width: 50px;
}

.container-serch-section form {
  display: flex;
  margin-top: 3px;
  border-radius: 10px;
}
.container-serch-section form input {
  width: 200px;
  height: 30px;
}
.container-serch-section .icon {
  color: white;
  cursor: pointer;
  background-color: goldenrod;
  border: 1px silver;
}

.presonal-page-icon {
  padding: 10px;
  text-transform: capitalize;
  font-weight: bold;
  font-size: 1.6em;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.presonal-page-icon:hover {
  transform: scale(1.1);
  transition: all 0.5s;
}

@media only screen and (max-width: 1200px) {
  .Navbar-section .select-element {
    opacity: 0;
  }
  .Navbar-section .select-element.active {
    opacity: 1;
    position: absolute;
    top: 60px;
    left: 0;
    display: block;
    width: 100vw;
    background-color: rgb(222, 176, 176);
    z-index: 100000;
    text-align: center;
    transition: all 0.5s;
  }

  .Navbar-section .select-element li {
    display: block;
    width: 100%;
  }

  .icon-bar {
    display: block;
  }
}

@media only screen and (max-width: 800px) {
  .Navbar-image {
    position: absolute;
    left: 10%;
  }
}
